
@charset "UTF-8";
body, div, span, header, footer, nav, section, aside, article, ul, dl, dt, dd, li, a, p, h1, h2, h3, h4, h5, h6, i, b, textarea, button, input, select, figure, figcaption {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  color: #333;
  box-sizing: border-box;
  font-family: Arial,Verdana,Sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
}
body:focus, div:focus, span:focus, header:focus, footer:focus, nav:focus, section:focus, aside:focus, article:focus, ul:focus, dl:focus, dt:focus, dd:focus, li:focus, a:focus, p:focus, h1:focus, h2:focus, h3:focus, h4:focus, h5:focus, h6:focus, i:focus, b:focus, textarea:focus, button:focus, input:focus, select:focus, figure:focus, figcaption:focus {
    outline: none;
}
body, html {
  height: 100%;
  overflow-x: initial !important;
  overflow-y: initial !important;
}
#root {
  height: 100%;
}

/* */
::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background-color: #F5F5F5;
}

/* +*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px transparent;
  border-radius: 10px;
  background-color: #F5F5F5;
}

/* +*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}
input[type="button"], input[type="submit"], input[type="search"], input[type="reset"] {
  -webkit-appearance: none;
}
textarea {
  -webkit-appearance: none;
}
html, body {
  width: 100%;
  background-color: #F5F5F5;
}
img {
  display: block;
}
.clearfix:after {
  content: '';
  display: block;
  clear: both;
}
.clearfix {
  zoom: 1;
}
.back_img {
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.margin {
  margin: 0 auto;
}
.left {
  float: left;
}
.right {
  float: right;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.paddingTop {
  padding-top: 1.95rem;
}
.paddingBottom {
  padding-bottom: 2.25rem;
}
.child-page {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  background-color: #f8f8f8;
  z-index: 1080;
}
.child-page.page-lv2 {
    z-index: 1090;
}
.router-fade-enter-active, .router-fade-leave-active {
  transition: opacity .2s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.router-slid-enter-active, .router-slid-leave-active {
  transition: all .4s;
}
.router-slid-enter, .router-slid-leave-active {
  transform: translate3d(2rem, 0, 0);
  opacity: 0;
}
.router-float-enter-active, .router-float-leave-active {
  transition: all .2s;
}
.router-float-enter, .router-float-leave-active {
  transform: translate3d(0, 30%, 0);
  opacity: 0;
}
.move-left-enter-active, .move-left-leave-active {
  transition: all .4s;
}
.move-left-enter, .router-left-leave-active {
  transform: translate3d(-100%, 0, 0);
}
.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}
@keyframes tada {
from {
    transform: scale3d(1, 1, 1);
}
10%, 20% {
    transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
}
30%, 50%, 70%, 90% {
    transform: scale3d(1.24, 1.24, 1.24) rotate3d(0, 0, 1, 3deg);
}
40%, 60%, 80% {
    transform: scale3d(1.24, 1.24, 1.24) rotate3d(0, 0, 1, -3deg);
}
to {
    transform: scale3d(1, 1, 1);
}
}
.tada {
  animation-name: tada;
}
.shadow {
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.156863), 0 3px 10px rgba(0, 0, 0, 0.227451);
}
.shadow-white {
  box-shadow: 0 3px 10px rgba(255, 255, 255, 0.156863), 0 3px 10px rgba(255, 255, 255, 0.227451);
}
.swiper-pagination-bullet-active {
  background: #8360ff !important;
}
.app {
  height: 100%;
  overflow-y: scroll;
}
#toasted-container {
  bottom: 1rem !important;
}
#toasted-container .toasted {
    margin: 0 auto !important;
    width: 6rem !important;
    height: 1.1rem !important;
    line-height: 1.6rem !important;
    font-size: .6rem !important;
    text-align: center !important;
    display: block !important;
    opacity: .9 !important;
}
.head_logo {
  left: .54rem;
  width: 1.4rem;
  height: 0.8rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("images/huipao.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 2;
}
.wrap {
  height: 100%;
  overflow: auto;
  background: #eee;
}



