.head_search .mu-text-field {
  padding-left: 3rem;
  margin-bottom: 0;
}
.head_search .mu-icon {
  font-size: 1.4rem;
  top: 12px;
  left: .6rem;
  color: #fff;
}
.head_search .mu-text-field-content {
  padding-top: 10px;
}
.head_search .mu-text-field.focus-state .mu-text-field-icon {
  color: #e91e63;
}
.head_search .mu-text-field-hint {
  padding-left: .4rem;
  font-size: .6rem;
  line-height: 1.2rem;
  color: #fff;
}
.head_search .mu-text-field-input {
  color: #fff;
  font-size: .6rem;
  padding: .1rem .4rem .1rem;
}
.head_search .mu-text-field-line {
  left: 2.6rem !important;
  background-color: #fff !important;
}
.head_search .mu-text-field-focus-line {
  left: 2.6rem !important;
  background-color: #e91e63 !important;
}
.head_search .mu-text-field-focus-line.focus {
  background-color: #e91e63 !important;
}
#head_top {
  background-color: #322758;
  position: fixed;
  z-index: 1090;
  left: 0;
  top: 0;
  width: 100%;
  height: 1.95rem;
}
.head_goback {
  position: absolute;
  top: .4rem;
  left: .2rem;
  line-height: 2.2rem;
  margin-left: .4rem;
  z-index: 1000;
}
.head_search {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0%, -50%);
  line-height: .34rem;
}
.head_search input {
    width: 11rem;
    height: 1.2rem;
    padding: .1rem .6rem .1rem;
    border-radius: .15rem;
}
.head_search .btn_search {
    right: -1.4rem;
    width: 0.8rem;
    height: 0.8rem;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.head_login {
  right: 0.55rem;
  font-size: 0.65rem;
  color: #fff;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.head_login .login_span {
    color: #fff;
}
.head_login .user_avatar {
    fill: #fff;
    width: 0.8rem;
    height: 0.8rem;
}
.head-name {
  position: absolute;
  color: #fff;
  line-height: 1.95rem;
  left: 14%;
  font-size: .6rem;
}
.head-action {
  position: absolute;
  color: #fff;
  line-height: 1.95rem;
  right: 6%;
  font-size: .6rem;
}
.head-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  color: #fff;
  text-align: center;
}
.head-title .title_text {
    font-size: 0.8rem;
    color: #fff;
    text-align: center;
    font-weight: bold;
}
.head-top-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  color: #fff;
  text-align: center;
}
.head-top-title .title_text {
    font-size: 0.6rem;
    color: #fff;
    text-align: center;
    font-weight: bold;
}



