
#BMessage {
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99999;
}
#BMessage .bmessage-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 6rem;
    min-height: 3rem;
    padding: .4rem;
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.7);
    text-align: center;
    line-height: 1.4rem;
    font-size: .6rem;
    font-weight: 500;
}
#BMessage .bmessage-container span {
      width: 100%;
      color: #fff;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity ease .3s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}



