
img {
  width: 100%;
}
.card {
  background-color: #4e4376;
  margin: 0;
}
.card .info {
    padding: .4rem .5rem;
}
.card .info .desc {
      height: 1.6rem;
      line-height: .8rem;
      color: #fff;
      font-size: .5rem;
      text-align: left;
      overflow: hidden;
}
.card .info .price {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -ms-flex-pack: justify;
          justify-content: space-between;
      -ms-flex-line-pack: start;
          align-content: flex-start;
      -ms-flex-align: center;
          align-items: center;
      height: 1rem;
}
.card .info .price p {
        display: inline-block;
        font-size: 16px;
}
.card .info .price span {
        display: inline-block;
        vertical-align: middle;
}
.card .info .price .price-count {
        color: #FFC107;
        font-size: .6rem;
}
.card .info .price .price-origin {
        position: relative;
        margin: 0;
        font-size: .5rem;
        color: #9e9e9e;
        padding-top: .1rem;
}
.card .info .price .price-origin:before {
          content: '';
          display: block;
          position: absolute;
          top: 50%;
          left: -5%;
          width: 115%;
          height: 2px;
          background: #9e9e9e;
}



